import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/style.scss';
import '../styles/style-desk-xl.scss';
import '../styles/style-desk-xxl.scss';
import '../styles/style-desk-wide.scss';
import '../styles/style-tablet.scss';
import '../styles/style-mobile.scss';
import '../styles/style-mobile-L.scss';
import '../styles/style-mobile-landscape.scss';
import '../styles/style-mobile-L-landscape.scss';
import '../styles/style-tablet-landscape.scss';
import Raster from '../assets/image/raster.png';

class ShowBluePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
        this.handleClick = this.handleClick.bind(this);
        this.text = React.createRef();
    }

    handleClick(event) {
    }
    render() {
        let animate = <div></div>;
        let blueText =
            <div>
                <div className="container static-page">
                    <div className="row" id="text-row">
                        <div className="col-12">
                            <div className="information-text" ref={node => this.text = node}>
                            Om Kreativa Europa
                            <br></br><br></br>
                            Kreativa Europa är EU-programmet för de kulturella och kreativa sektorerna.
                            <br></br><br></br>
                            Kreativa Europa består av delprogrammen Kultur och MEDIA och ett sektorsövergripande programområde. Kulturrådet och Filminstitutet delar uppdraget som Kreativa Europa Desk Sverige. Vi svarar på frågor om programmet och ger rådgivning vid ansökan. Kulturrådet ansvarar för delprogrammet Kultur och Filminstitutet för delprogrammet MEDIA och båda för det sektorsövergripande programområdet, inklusive Kreatörslånet för kulturella och kreativa företag.
                            <br></br><br></br>
                            <span className="cursive-eng-info-text">Creative Europe is the Europan Commission framework Programme for the cultural and creative sectors. In Sweden the mission as Creative Europe Desk is carried out by the Swedish Arts Council and the Swedish Film Institute.</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        /**
         * check if first time pressing a link. 
         * If this is the case animate 
         * from: split
         * to : center
         * -------
         * ELSE animate
         * from: center
         * to center
         * and change background-color
         * from: full
         * to: transparent
         */
        const animationDelay = 250;
        if (this.props.counter > 1) {
            animate = <div>
                {/*<div className="raster raster-static-blue">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>*/}
                <div className="rect-anim static-blue-rect" id="blueRect">
                    
                    {blueText}
                </div>
            </div>
        } else {
            animate = <div>
                <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="green-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim green" id="greenRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="green-raster-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="pink-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim pink" id="pinkRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="pink-raster-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="blue-transition-ontop"
                    unmountOnExit
                    appear
                    // This will create an animation from full color as in prev state to transparent
                    //onEntered={()=> document.getElementById("abc").hidden = true}
                    exit
                >
                    <div className="rect-anim blue" id="blueRect">

                        {blueText}
                    </div>
                </CSSTransition>
               {/* <CSSTransition
                    in={this.props.blue}
                    timeout={animationDelay}
                    classNames="blue-raster-transition-ontop"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-static-blue">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
                </CSSTransition>*/}
            </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowBluePage;





