import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/style.scss';
import '../styles/style-desk-xl.scss';
import '../styles/style-desk-xxl.scss';
import '../styles/style-desk-wide.scss';
import '../styles/style-tablet.scss';
import '../styles/style-mobile.scss';
import '../styles/style-mobile-L.scss';
import '../styles/style-mobile-landscape.scss';
import '../styles/style-mobile-L-landscape.scss';
import '../styles/style-tablet-landscape.scss';
import Raster from '../assets/image/raster.png';
import kulturEurPic from '../assets/image/Kreativa_Europa_Kultur.png';
import kulturRadet from '../assets/image/Kulturradet.gif';

class ShowPinkPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
        this.text = React.createRef();
    }

    render() {
        let animate = <div></div>;
        let pinkText = <div>
        <div className="container static-page" >
            <div className="row">
                <div className="col-12">
                    <div className="information-text" id="info-text" ref={node => this.text = node}>
                        Kultur<br></br><br></br>
                        Delprogrammet Kultur vänder sig till aktörer inom musik, scenkonst, bildkonst,
                        design, konsthantverk, arkitektur, litteratur, tidskrifter, radio, arkiv, bibliotek, museer, kulturarv och kulturmiljö. 
                        <br></br>
                        <br></br>
                        Stöd finns för europeiska samarbetsprojekt, nätverk och
                        plattformar, översättning och spridning av europeisk skönlitteratur samt särskilda pilotsatsningar.
                    </div>
                    <br></br>
                </div>
            </div>
            {/* <div className="row justify-content-around align-items-center link-row hidden-xs  visable-xs">
                <div className="col-6 white-nowrap">
                <i className="fas fa-chevron-right"></i><span>  </span><a href="https://www.kulturradet.se/kreativa-europa/" className="static-page-link">Till Kreativa Europa Kultur</a>
                </div>
                <div className="col-2">
                    <img src={kulturRadet} className="icon-pic-kultur" alt="not loading"></img>
                </div>
                <div className="col-3">
                    <a href="https://www.kulturradet.se/kreativa-europa/"><img src={kulturEurPic} className="icon-pic" alt="not loading"></img></a>
                </div>
            </div>
            <br></br> */}
            <div className="row justify-content-around align-items-center link-row ">
                <div className="col-12 white-nowrap">
                <i className="fas fa-chevron-right"></i><span>  </span><a href="https://www.kulturradet.se/kreativa-europa/" className="static-page-link">Till Kreativa Europa Kultur</a>
            </div>
            <div className="spacer"></div>
            </div>
            <div className="row justify-content-start align-items-end ">
                <div className="col-4 text-center">
                    <img src={kulturRadet} className="icon-pic-kultur" alt="not loading"></img>
                </div>
                <div className="col-3 text-center">
                    <a href="https://www.kulturradet.se/kreativa-europa/"><img src={kulturEurPic} className="icon-pic" alt="not loading"></img></a>
                </div>
            </div>
            
        </div>
        </div>

        /**
         * check if first time pressing a link.
         * If this is the case animate
         * from: split
         * to : center
         * -------
         * ELSE animate
         * from: center
         * to center
         */
        const animationDelay = 250;
        if (this.props.counter > 1) {
            animate = <div>
                {/* <div className="raster raster-static-pink">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div> */}
                <div className="rect-anim static-pink-rect" id="pinkRect">
                    {pinkText}
                </div>
            </div>
        } else {
            animate = <div>
            <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="green-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim green" id="greenRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="green-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="blue-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim blue" id="blueRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="blue-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="pink-transition-ontop"
                unmountOnExit
                appear
                // This will create an animation from full color as in prev state to transparent
                
                exit
            >
                <div className="rect-anim pink" id="pinkRect">
                    {pinkText}
                </div>
            </CSSTransition>
            {/* <CSSTransition
                in={this.props.pink}
                timeout={animationDelay}
                classNames="pink-raster-transition-ontop"
                unmountOnExit
                appear
                exit
            >
                <div className="raster raster-static-pink">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition> */}
        </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowPinkPage;