import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/style.scss';
import '../styles/style-desk-xl.scss';
import '../styles/style-desk-xxl.scss';
import '../styles/style-desk-wide.scss';
import '../styles/style-tablet.scss';
import '../styles/style-mobile.scss';
import '../styles/style-mobile-L.scss';
import '../styles/style-mobile-landscape.scss';
import '../styles/style-mobile-L-landscape.scss';
import '../styles/style-tablet-landscape.scss';
import skapa from '../assets/image/skapa.png';
import Raster from '../assets/image/raster.png';
import tank from '../assets/image/tank.png';
import dela from '../assets/image/dela.png';

class WelcomeRects extends Component {

    render() {
        let tankImage, delaImage, skapaImage;
        tankImage =
            <div className="img-wrapper">
                <img src={tank} width="100%" height="100%" alt="img not loading"></img>
            </div>
        delaImage =
            <div className="img-wrapper">
                <img src={dela} width="100%" height="100%" alt="img not loading"></img>
            </div>
        skapaImage =
            <div className="img-wrapper">
                <img src={skapa} width="100%" height="100%" alt="img not loading"></img>
            </div>

        return (
            <div>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="green-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim green" id="greenRect" >
                        {delaImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="green-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-green">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>

                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="pink-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim pink" id="pinkRect" >
                        {tankImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="pink-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-pink">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>

                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="blue-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim blue" id="blueRect" >
                        {skapaImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="blue-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-blue">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
            </div>
        );
    }
}

export default WelcomeRects;





