import React from 'react';
import Main from './components/Main';
import './App.css';
import classNames from 'classnames';
import './styles/style-desk-xl.scss';
import './styles/style-desk-xxl.scss';
import './styles/style-desk-wide.scss';
import './styles/style-tablet.scss';
import './styles/style-mobile.scss';
import './styles/style-mobile-L.scss';
import './styles/style-mobile-L-landscape.scss';
import './styles/style-tablet-landscape.scss';

import backgroundVideo from './assets/video/background.mp4';
import ReactPlayer from 'react-player';
import Logo from './assets/image/Kreativa_Europa.png';
import HamburgerMenu from './components/hamburgerMenu/hamburgerMenu.js'
import OutsideAlerter from "./components/OutsideAlerter";



class App extends React.Component {
  constructor() {
    super();
    this.state = {
      blue: false,
      green: false,
      pink: false,
      welcome: true,
      counter: 0,
      media: 'desktop',
      open: false
    }
    this.handleClickBlue = this.handleClickBlue.bind(this);
    this.handleClickGreen = this.handleClickGreen.bind(this);
    this.handleClickPink = this.handleClickPink.bind(this);
    this.handleMenuClick = this.handleMenuClick.bind(this);

    this.greenButton = React.createRef();
    this.pinkButton = React.createRef();
    this.pinkButton = React.createRef();
  }

  componentDidMount() {}

  handleWindowSizeChange = () => {};

  // Handle if user clicked the X button for menu open
  handleMenuClick(){
    if(!this.open){
      this.setState({ open: false });
    } else {
      this.setState({ open: true });
    }
  }

  // Handle clicks on burger menu
  handleClick() {  
    this.setState({
      open: !this.state.open
    });
  }

  handleClickBlue() {
    this.setState({
      blue: true,
      green: false,
      pink: false,
      welcome: false,
      open: false
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
  }

  handleClickGreen() {
    this.setState({
      blue: false,
      green: true,
      pink: false,
      welcome: false,
      open: false
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
  }

  handleClickPink() {
    this.setState({
      blue: false,
      green: false,
      pink: true,
      welcome: false,
      open: false
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
  }

  // Render buttons and Main with passed down boolean values for colors.
  render() {
    // Buttons 
    var greenButtonClass = classNames({
      btn: true,
      'button-green': true,
      'active': this.state.green,
    });
    var blueButtonClass = classNames({
      btn: true,
      'button-blue': true,
      'active': this.state.blue,
    });
    var pinkButtonClass = classNames({
      btn: true,
      'button-pink': true,
      'active': this.state.pink,
    });
    // mobile version
    var smallGreenButtonClass = classNames({
      smallbtn: true,
      
      'active': this.state.green,
    });
    var smallBlueButtonClass = classNames({
      smallbtn: true,
      
      'active': this.state.blue,
    });
    var smallPinkButtonClass = classNames({
      smallbtn: true,
      
      'active': this.state.pink,
    });
    let menu = null;
    if(this.state.open){
      menu = <div className="menu-box visable-xs" ref={this.menu}>
        <div className="row justify-content-center">
          <div className="col-12 menu-items">
            <ul className="smallMenu">
            <li><button className={smallBlueButtonClass} onClick={this.handleClickBlue} ref={this.blueButton}>Om Kreativa Europa</button></li>
            <li><button className={smallPinkButtonClass} onClick={this.handleClickPink} ref={this.pinkButton}>Kultur</button></li>
            <li><button className={smallGreenButtonClass} onClick={this.handleClickGreen} ref={this.greenButton}>MEDIA</button></li>
            </ul>
          </div>
        </div>
      </div>
    } else {
      menu = <div></div>
    }
    return (
      //playbackRate="0.7" Setting in ReactPlayer to slow down playback rate
      <div >
        <ReactPlayer
          url={[backgroundVideo]}
          // config={
          //   {
          //     attributes: {
          //       style: { height: {wh}, width: {ww} }}
          //   }
          // }
          playing
          loop
          autoPlay
          muted
          width="auto"
          height="100%"
          fluid="false"
          className="background-video"
        />

        <nav className="navbar navbar-expand navbar-light bg-transparent hidden-xs">
          <a className="navbar-brand" href="/">
            <img src={Logo} alt="not loading" className="logo-image "></img>
          </a>
          <ul className="navbar-nav ml-auto" ref={this.buttons}>
            <li><button className={blueButtonClass} onClick={this.handleClickBlue} ref={this.blueButton}>Om Kreativa Europa</button></li>
            <li><button className={pinkButtonClass} onClick={this.handleClickPink} ref={this.pinkButton}>Kultur</button></li>
            <li><button className={greenButtonClass} onClick={this.handleClickGreen} ref={this.greenButton}>MEDIA</button></li>
          </ul>
        </nav>
        <div className="row justify-content-between menu-container">
          <div className="col-4">
          <a className="navbar-brand" href="/">
            <img src={Logo} alt="not loading" className="logo-image visable-xs hidden-xs-tab"></img>
          </a>
          </div>
          <div className="col-1 menu-col">
            <HamburgerMenu
                  isOpen={this.state.open}
                  menuClicked={this.handleClick.bind(this)}
                  width={25}
                  height={19}
                  strokeWidth={2}
                  rotate={0}
                  color='white'
                  borderRadius={0}
                  animationDuration={0.5}
                />
            <OutsideAlerter open={this.state.open} handleMenuClick={this.handleMenuClick}>
              {menu}
            </OutsideAlerter>                
          </div>
        </div>
        <Main blue={this.state.blue}
          green={this.state.green}
          pink={this.state.pink}
          welcome={this.state.welcome}
          counter={this.state.counter}
          pinkClick={this.handleClickPink}
          blueClick={this.handleClickBlue}
          greenClick={this.handleClickGreen}
        ></Main>
      </div>
    );
  }
}

export default App;