import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/style.scss';
import '../styles/style-desk-xl.scss';
import '../styles/style-desk-xxl.scss';
import '../styles/style-desk-wide.scss';
import '../styles/style-tablet.scss';
import '../styles/style-mobile.scss';
import '../styles/style-mobile-L.scss';
import '../styles/style-mobile-landscape.scss';
import '../styles/style-mobile-L-landscape.scss';
import '../styles/style-tablet-landscape.scss';
import Raster from '../assets/image/raster.png';
import mediaEurPic from '../assets/image/Kreativa_Europa_MEDIA.png';
import filminstitutet from '../assets/image/SFI.png';

class ShowGreenPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
        this.text = React.createRef();
    }

    render() {
        let animate = <div></div>;
        let greenText = <div>
            <div className="container static-page">
                <div className="row" >
                    <div className="col-12">
                        <div className="information-text" id="info-text" ref={node => this.text = node}>
                            MEDIA<br></br><br></br>
                            Delprogrammet MEDIA vänder sig till aktörer inom den audiovisuella sektorn
                            (film, tv och dataspel). 
                            <br></br>
                            <br></br>
                            Stöd kan sökas av produktionsbolag, spelutvecklare,
                            utbildningsorganisationer, distributörer, filmfestivaler och andra organisationer inom sektorn.
                        </div>
                        <br className="hidden-xs"></br>
                        <br className="hidden-xs"></br>
                    </div>
                </div>
                {/* <div className="row justify-content-around align-items-center link-row hidden-xs hidden-xs-tab">
                    <div className="col-6">
                    <i className="fas fa-chevron-right"></i><span>  </span><a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/" className="static-page-link">Till Kreativa Europa MEDIA</a>
                    </div>
                    <div className="col-2">
                        <img src={filminstitutet} className="icon-pic-filminstitutet" alt="not loading"></img>
                    </div>
                    <div className="col-3">
                        <a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/"><img src={mediaEurPic} className="icon-pic" alt="not loading"></img></a>
                    </div>
                </div>
                <br></br> */}
                <div className="row justify-content-around align-items-center link-row">
                    <div className="col-12">
                    <i className="fas fa-chevron-right"></i><span>  </span><a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/" className="static-page-link">Till Kreativa Europa MEDIA</a>
                </div>
                <div className="spacer"></div>
                </div>
                <div className="row justify-content-start align-items-end ">
                    <div className="col-4 text-center">
                        <img src={filminstitutet} className="icon-pic-filminstitutet" alt="not loading"></img>
                    </div>
                    <div className="col-3 text-center">
                    <a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/"><img src={mediaEurPic} className="icon-pic" alt="not loading"></img></a>
                    </div>
                </div>
            </div>
        </div>
        const animationDelay = 250;
        if (this.props.counter > 1) {
            animate = <div>
                {/* <div className="raster raster-static-green">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div> */}
                <div className="rect-anim static-green-rect" id="greenRect">
                    {greenText}
                </div>
            </div>
        } else {
            animate = <div>
            <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="pink-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim pink" id="pinkRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="pink-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="blue-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim blue" id="blueRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="blue-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="green-transition-ontop"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim green" id="greenRect">
                    {greenText}
                </div>
            </CSSTransition>
            {/* <CSSTransition
                in={this.props.green}
                timeout={animationDelay}
                classNames="green-raster-transition-ontop"
                unmountOnExit
                appear
                exit
            >
                <div className="raster raster-static-green">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition> */}
        </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowGreenPage;